<template>
  <div class="hello">
  <el-input v-model="input" placeholder="请输入用餐人数"></el-input>
  <el-input v-model="search"  placeholder="请输入关键字" class='key-words'></el-input>
 
  <template v-if='searchList.length && showSearch'>
    <!-- <ul v-for="detail in searchList">
        <li>{{detail.name}}</li>
     </ul> -->
   <el-table :data="searchList" style="width: 100%">
    <el-table-column label="菜名" >
      <template slot-scope="scope">
        <p> {{ scope.row.name }}</p>
      </template>
    </el-table-column>
    <el-table-column label="价格" >
      <template slot-scope="scope">
       <p>{{scope.row.price}}</p>
      </template>
    </el-table-column>
    <el-table-column label="类型"  >
      <template slot-scope="scope">
       <p>{{scope.row.type/1 == 0 ? '大荤': scope.row.type/1 == 1 ? '小荤': scope.row.type/1 == 2 ? '素菜': '汤'}}</p>
      </template>
    </el-table-column>
    <el-table-column label="操作">
      <template slot-scope="scope">
        <el-button  size="mini"  type="danger"  @click="handleAdd(scope.$index, scope.row)">添加</el-button>
      </template>
    </el-table-column>
  </el-table>

  </template>
  <template v-else>
    
  
   <el-table  :data="list" style="width: 100%">
    <el-table-column label="菜名" >
      <template slot-scope="scope">
        <p> {{ scope.row.name }}</p>
      </template>
    </el-table-column>
    <el-table-column label="价格" >
      <template slot-scope="scope">
       <p>{{scope.row.price}}</p>
      </template>
    </el-table-column>
    <el-table-column label="类型"  >
      <template slot-scope="scope">
       <p>{{scope.row.type/1 == 0 ? '大荤': scope.row.type/1 == 1 ? '小荤': scope.row.type/1 == 2 ? '素菜': '汤'}}</p>
      </template>
    </el-table-column>
    <el-table-column label="操作">
      <template slot-scope="scope">
        <el-button  size="mini"  type="danger"  @click="handleDelete(scope.$index, scope.row)">删除</el-button>
      </template>
    </el-table-column>
  </el-table>
</template>
  <p>共计消费：{{totalPrice}}，人均消费：{{ input ? totalPrice/(input / 1) : totalPrice}}</p>
      <el-button class='five' type="warning" round  @click="fiveList">随机五个菜</el-button>
      <el-button class='' type="warning" round  @click="clear">一键删除</el-button>
      <el-button type="primary" round @click="type0">随机一个大荤</el-button>
      <el-button type="primary" round @click="type1">随机一个小荤</el-button>
      <el-button type="primary" round @click="type2">随机一个素菜</el-button>
      <el-button type="primary" round @click="type3">随机一个汤类</el-button>
      <el-button type="primary" round  @click="one">随机一个菜</el-button>

  </div>
</template>

<script>
import json from '../ft_food.json'
import $ from 'jquery'
export default {
  name: 'HelloWorld',
  data () {
    return {
      allData : json.RECORDS,
      json:json.RECORDS,
      list:[],
      input:'',
      search:'',
      showSearch:true,
      tableShow:true
      
    }
  },
  methods: {
    handleAdd(index,row){
      console.log(row)
      this.list.push(row)
      //this.search = row.name
      this.search = ""
      this.showSearch = false
      this.tableShow = true
    },
    fiveList(e){
      for(let i = 0; i < 5; i++){
        this.one()
      }
    },
    //随机一个热菜
    type0(){
      var arr = this.common(0)
      if (!arr.length) return 
      $('.five').attr("disabled",true)
      var index  = Math.floor(Math.random() * arr.length)
      this.list.push(arr.splice(index,1)[0])
      arr.forEach(e =>{
        this.json.push(e)
      })
      console.log(this.json.length)

    },
     type1(){
      var arr = this.common(1)
      if (!arr.length) return 
       $('.five').attr("disabled",true)
      var index  = Math.floor(Math.random() * arr.length)
      this.list.push(arr.splice(index,1)[0])

      arr.forEach(e =>{
        this.json.push(e)
      })
      console.log(this.json.length)
    },
     type2(){
      var arr = this.common(2)
      if (!arr.length) return  
       $('.five').attr("disabled",true)
      var index  = Math.floor(Math.random() * arr.length)
      this.list.push(arr.splice(index,1)[0])
      arr.forEach(e =>{
        this.json.push(e)
      })
      console.log(this.json.length)
    },
     type3(){
      var arr = this.common(3)
      if (!arr.length) return 
       $('.five').attr("disabled",true)
      var index  = Math.floor(Math.random() * arr.length)
      this.list.push(arr.splice(index,1)[0])
      //this.json.concat(arr)
       arr.forEach(e =>{
        this.json.push(e)
      })
      console.log(this.json.length)
    },
     type4(){
      var arr = this.common(4)
      if (!arr.length) return 
      $('.five').attr("disabled",true)
      var index  = Math.floor(Math.random() * arr.length)
      this.list.push(arr.splice(index,1)[0])
      arr.forEach(e =>{
        this.json.push(e)
      })
      //this.json.concat(arr)
      console.log(this.json.length)
    },
    clear(){
      // for(let i = 0; i < this.list.length;i++){
      //   this.json.push(this.list[i]);
      // }
      //this.json.concat(this.list)
      //console.log(this.json.length)
      this.list = []
      $('.five').removeAttr('disabled')
    },
    //随机一个凉菜
    onecool(){

    },
    //随机一个汤
    onesour(){},
    //随机一个菜
    one(){
      //console.log(arguments)
      $('.five').attr("disabled",true)
      var index  = Math.floor(Math.random() * this.json.length)
      this.list.push(this.json.splice(index,1)[0])
    },
    //common
    common(type){
       $('.five').attr("disabled",true)
      let total = [];
      console.log(this.json.length)
      for(let i = 0; i < this.json.length;i++){

        if(this.json[i].type / 1 == type){
          console.log(this.json[i].type)
         
          total.push(this.json.splice(i,1)[0])
        }
      }
      return total
    },
    //
    handleDelete(index, row) {
        console.log(this.json.length + '=====json====');
        //this.json.push(this.list.splice(index,1)[0]);
        this.list.splice(index,1)
        if(!this.list.length){
          $('.five').removeAttr('disabled')
        }
      }
  },
  watch:{
    search(val,oldVal){
      console.log(val+"===>"+oldVal)
      if(oldVal.length > 0 && val.length == 0){
        this.showSearch = true
        this.tableShow = true
      }
    }

  },
  computed:{
    searchList(){

      let arr = []
      if(!this.search) return [];
      let json = this.allData
      for(let i = 0; i < json.length;i++){
        arr = json.filter(e => 
          e.name.indexOf(this.search) > -1
        )
      }
      return arr
    },
    totalPrice:function(){
      //console.log(this)
      let total = 0;
      for (let i in this.list) {
        total += this.list[i].price / 1;
      }
      return total;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  scoped>
  .hello{
    width:100%;
  }
  button{
    width: 100%;
    margin: 10px 0px !important;
  }

</style>
<style>
  .cell {
    text-align: center;
}
.key-words{
  margin-top:20px
}
</style>





